










/***
 * 面包屑导航
 */
import { Component, Prop, Vue, Watch } from "vue-property-decorator";

@Component
export default class MyBreadcrumb extends Vue {
  private matched: Array<any> = [];

  @Watch("$route.path")
  changRoute(val: any) {
    this.matched = this.$route.matched;
  }

  created() {
    this.matched = this.$route.matched;
  }
}
